import ReactDOM from 'react-dom';
import React, {useState, useEffect} from 'react';
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import './categorys-style.scss';
import 'react-tabs/style/react-tabs.css';
import { List, Filter} from '../index';
import Table from '../table';
class FilterTabs extends React.Component {
    constructor(props) {
        super();
        this.state = {
            // Takes active tab from props if it is defined there
            activeTab: props.activeTab || 0
        };
        
        // Bind the handleSelect function already here (not in the render function)
        this.handleSelect = this.handleSelect.bind(this);
    }

    render() {
      return (
        <Tabs activeKey={this.state.activeTab} onSelect={this.handleSelect} className='container'>
            <TabList>
                <Tab eventKey={1} title="Pacientes">Pacientes</Tab>
                <Tab eventKey={2} title="Modo VCV">Modo VCV</Tab>
                <Tab eventKey={3} title="Modo PCV" >Modo PCV</Tab>
                <Tab eventKey={4} title="Modo PSV">Modo PSV</Tab>
                <Tab eventKey={5} title="Tab 5">Isa 05</Tab>
            </TabList>

            <TabPanel eventKey={1} title="Pacientes">
                <Table />
            </TabPanel>
            <TabPanel eventKey = {2} title="Modo VCV" >
                <Table>
                    <Filter
                        
                    />
                </Table>
            </TabPanel>
        </Tabs>
      );
    }
    
    handleSelect(selectedTab) {
    //   The active tab must be set into the state so that
    //   the Tabs component knows about the change and re-renders.
      this.setState({
        activeTab: selectedTab
      });
    
    }

    // handleSelect(index) {

    //     if (index === 0) {
    //         this.setState({
    //             activeTab: index
    //         })
    //     }   
    //     else if (index === 1) {
    //         this.setState({
    //             activeTab: index
    //         })
    //     }
    //     else if (index === 2) {
    //         this.setState({
    //             activeTab: index
    //         })
    //     }
    //     else {
    //         this.setState({
    //             activeTab: index
    //         })
    //     }
    // }
    

  }
  
//   ReactDOM.render(
//     <MyComponent activeTab={4} />,
//     document.getElementsByClassName('container')[0]
//   );
  
  export default FilterTabs
